.button{
	margin-left: 20px;
	height: 50px;
	cursor: pointer;
	background-color: beige;
	font-size: 1.5rem;
	padding: 10px 15px;
	border: none;
	border-radius: 5px;
	transition: all 0.3s ease 0s;
}

.button:hover {
	background-color: rgb(240, 240, 155);
 }

